import "./App.css";
import { Button, Modal, Alert, Table } from "antd";
import { useState, useEffect } from "react";
import card from "./f61e3d0639493e822776ab32c9322f49--joker-card-trump-card.jpg";

function App() {
  const [visible, setVisible] = useState(false);
  const [qtyPlayers, setQtyPlayers] = useState(0);
  const [namePlayers, setNamePlayers] = useState([]);
  const [showButton, setShowButton] = useState(false);
  const [playerOne, setPlayerOne] = useState(0);
  const [playerTwo, setPlayerTwo] = useState(0);
  const [playerThree, setPlayerThree] = useState(0);
  const [playerFour, setPlayerFour] = useState(0);

  const [currentPlayerTwo, setCurrentPlayerTwo] = useState(0);
  const [currentPlayerOne, setCurrentPlayerOne] = useState(0);
  const [currentPlayerThree, setCurrentPlayerThree] = useState(0);
  const [currentPlayerFour, setCurrentPlayerFour] = useState(0);

  const [playerOneResult, setPlayerOneResult] = useState(0);
  const [playerTwoResult, setPlayerTwoResult] = useState(0);
  const [playerThreeResult, setPlayerThreeResult] = useState(0);
  const [playerFourResult, setPlayerFourResult] = useState(0);

  const [dataSource, setDataSource] = useState([{}]);

  const onChange = (e) => {
    setQtyPlayers(e.target.value);
  };

  const createPlayersNames = (qtyPlayers) => {
    let count = 0;
    let arrayNames = [];
    while (qtyPlayers > count) {
      let names = prompt("Ingresa el nombre");
      arrayNames.push(names);
      setNamePlayers(arrayNames);
      count++;
    }
    setVisible(false);
    setShowButton(true);
  };

  const sumPlayerOne = () => {
    setCurrentPlayerOne(playerOne);
    let result = parseInt(playerOne) + parseInt(currentPlayerOne);
    setPlayerOneResult(result);
    setPlayerOne(0);
    setCurrentPlayerOne(result);
  };

  const sumPlayerTwo = () => {
    setCurrentPlayerTwo(playerTwo);
    let result = parseInt(playerTwo) + parseInt(currentPlayerTwo);
    setPlayerTwoResult(result);
    setPlayerTwo(0);
    setCurrentPlayerTwo(result);
  };

  const sumPlayerThree = () => {
    setCurrentPlayerThree(playerThree);
    let result = parseInt(playerThree) + parseInt(currentPlayerThree);
    setPlayerThreeResult(result);
    setPlayerThree(0);
    setCurrentPlayerThree(result);
  };

  const sumPlayerFour = () => {
    setCurrentPlayerFour(playerFour);
    let result = parseInt(playerFour) + parseInt(currentPlayerFour);
    setPlayerFourResult(result);
    setPlayerFour(0);
    setCurrentPlayerFour(result);
  };

  const columns = [
    {
      title: "Nombre",
      dataIndex: "nombre",
      key: "nombre",
    },
    {
      title: "Puntaje",
      dataIndex: "puntaje",
      key: "puntaje",
      defaultSortOrder: "descend",
      sorter: (a, b) => a.puntaje - b.puntaje,
    },
  ];

  useEffect(() => {
    if (qtyPlayers === "2") {
      setDataSource([
        {
          key: "1",
          nombre: namePlayers[0],
          puntaje: playerOneResult,
        },
        {
          key: "2",
          nombre: namePlayers[1],
          puntaje: playerTwoResult,
        },
      ]);
    }
    if (qtyPlayers === "3") {
      setDataSource([
        {
          key: "1",
          nombre: namePlayers[0],
          puntaje: playerOneResult,
        },
        {
          key: "2",
          nombre: namePlayers[1],
          puntaje: playerTwoResult,
        },

        {
          key: "3",
          nombre: namePlayers[2],
          puntaje: playerThreeResult,
        },
      ]);
    }
    if (qtyPlayers === "4") {
      setDataSource([
        {
          key: "1",
          nombre: namePlayers[0],
          puntaje: playerOneResult,
        },
        {
          key: "2",
          nombre: namePlayers[1],
          puntaje: playerTwoResult,
        },

        {
          key: "3",
          nombre: namePlayers[2],
          puntaje: playerThreeResult,
        },

        {
          key: "4",
          nombre: namePlayers[3],
          puntaje: playerFourResult,
        },
      ]);
    }
  }, [playerTwoResult, playerOneResult, playerFourResult, playerThreeResult]);

  return (
    <div className="App">
      <Button
        type="primary"
        className="buttonGame"
        hidden={showButton}
        onClick={() => setVisible(true)}
      >
        Empezar partida
      </Button>
      <Modal
        title="PREPARANDO PARTIDA"
        centered
        visible={visible}
        onOk={() => createPlayersNames(qtyPlayers)}
        onCancel={() => setVisible(false)}
      >
        {qtyPlayers >= 5 ? (
          <Alert message="Cantidad maxima de jugadores 4" type="error" />
        ) : (
          ""
        )}
        <p>Cantidad de jugadores</p>
        <input
          type="number"
          min={2}
          max={4}
          onChange={(e) => onChange(e)}
          className="inputQty"
        />
      </Modal>
      <div>
        {qtyPlayers === "2" ? (
          <>
            <label style={{ marginTop: "10px" }}>{namePlayers[0]}</label>
            <input
              type="number"
              min={0}
              defaultValue={0}
              onChange={(e) => {
                setPlayerOne(e.target.value);
              }}
              className="inputQty"
            />
            <Button
              type="primary"
              className="buttonGame"
              onClick={() => sumPlayerOne()}
            >
              Sumar
            </Button>
            <br />
            <br />
            <br />
            <label>{namePlayers[1]}</label>
            <input
              type="number"
              min={0}
              defaultValue={0}
              onChange={(e) => {
                setPlayerTwo(e.target.value);
              }}
              className="inputQty"
            />
            <Button
              type="primary"
              className="buttonGame"
              onClick={() => sumPlayerTwo()}
            >
              Sumar
            </Button>
          </>
        ) : (
          ""
        )}
        {qtyPlayers === "3" ? (
          <>
            <label style={{ marginTop: "10px" }}>{namePlayers[0]}</label>
            <input
              type="number"
              min={0}
              defaultValue={0}
              onChange={(e) => {
                setPlayerOne(e.target.value);
              }}
              className="inputQty"
            />
            <Button
              type="primary"
              className="buttonGame"
              onClick={() => sumPlayerOne()}
            >
              Sumar
            </Button>
            <br />
            <br />
            <br />
            <label>{namePlayers[1]}</label>
            <input
              type="number"
              min={0}
              defaultValue={0}
              onChange={(e) => {
                setPlayerTwo(e.target.value);
              }}
              className="inputQty"
            />
            <Button
              type="primary"
              className="buttonGame"
              onClick={() => sumPlayerTwo()}
            >
              Sumar
            </Button>
            <br />
            <br />
            <br />
            <label>{namePlayers[2]}</label>
            <input
              type="number"
              min={0}
              defaultValue={0}
              onChange={(e) => {
                setPlayerThree(e.target.value);
              }}
              className="inputQty"
            />
            <Button
              type="primary"
              className="buttonGame"
              onClick={() => sumPlayerThree()}
            >
              Sumar
            </Button>
          </>
        ) : (
          ""
        )}
        {qtyPlayers === "4" ? (
          <>
            <label style={{ marginTop: "10px" }}>{namePlayers[0]}</label>
            <input
              type="number"
              min={0}
              defaultValue={0}
              onChange={(e) => {
                setPlayerOne(e.target.value);
              }}
              className="inputQty"
            />
            <Button
              type="primary"
              className="buttonGame"
              onClick={() => sumPlayerOne()}
            >
              Sumar
            </Button>
            <br />
            <br />
            <br />
            <label>{namePlayers[1]}</label>
            <input
              type="number"
              min={0}
              defaultValue={0}
              onChange={(e) => {
                setPlayerTwo(e.target.value);
              }}
              className="inputQty"
            />
            <Button
              type="primary"
              className="buttonGame"
              onClick={() => sumPlayerTwo()}
            >
              Sumar
            </Button>
            <br />
            <br />
            <br />
            <label>{namePlayers[2]}</label>
            <input
              type="number"
              min={0}
              defaultValue={0}
              onChange={(e) => {
                setPlayerThree(e.target.value);
              }}
              className="inputQty"
            />
            <Button
              type="primary"
              className="buttonGame"
              onClick={() => sumPlayerThree()}
            >
              Sumar
            </Button>
            <br />
            <br />
            <br />
            <label>{namePlayers[3]}</label>
            <input
              type="number"
              min={0}
              defaultValue={0}
              onChange={(e) => {
                setPlayerFour(e.target.value);
              }}
              className="inputQty"
            />
            <Button
              type="primary"
              className="buttonGame"
              onClick={() => sumPlayerFour()}
            >
              Sumar
            </Button>
          </>
        ) : (
          ""
        )}
      </div>
      <br />
      <div hidden={!showButton}>
        <center>
          <label>Tabla De Puntajes</label>
        </center>
        <hr />
        <Table
          pagination={{ position: ["none"] }}
          dataSource={dataSource}
          columns={columns}
        />
      </div>
    </div>
  );
}

export default App;
